import { Box, Flex } from '@chakra-ui/react';
import { Link } from 'react-router-dom';
import { Outlet } from 'react-router-dom';

export const Layout = () => {
  return (
    <Box w="full" h="full">
      {/*  */}
      <Box pos="fixed" left={0} w="20%" h="full" bg="green.100">
        <Link to="/home">
          <Box></Box>
        </Link>
        <Link to="/info">
          <Box></Box>
        </Link>
      </Box>

      {/*  */}
      <Flex>
        <Box w="20%" h="full" />
        <Box w="80%" h="100vh" overflow="auto">
          <Outlet />
        </Box>
      </Flex>
    </Box>
  );
};
